.container {
  margin-bottom: 45vw;
  @media screen and (max-width: 1920px) {
    margin-bottom: 35vw;
  }
  @media screen and (max-width: 1440px) {
    margin-bottom: 20vw;
  }
  @media screen and (max-width: 768px) {
    margin-bottom: 35vw;
  }
  @media screen and (max-width: 425px) {
    margin-bottom: 80vw;
  }
  & > div {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    .imgs {
      display: grid;
      grid-template-columns: 1fr 1fr;
      column-gap: 10px;
      row-gap: 10px;
      width: 35%;
      padding: 50px;
      @media screen and (max-width: 768px) {
        padding: 10px 20px;
        width: 30%;
      }
      @media screen and (max-width: 425px) {
        grid-template-columns: 1fr;
      }
      img {
        width: 100%;
      }
    }
    .text {
      width: 60%;
      @media screen and (max-width: 768px) {
        width: 70%;
      }
      p {
        letter-spacing: 2px;
        text-align: center;
        margin-bottom: 20px;
        @media screen and (max-width: 1024px) {
          margin-bottom: 10px;
        }
        span {
          color: #ff9c00;
          font-family: "comic";
          font-weight: 700;
          -webkit-text-stroke: 1px rgb(0, 0, 0);
          @media screen and (max-width: 425px) {
            -webkit-text-stroke: 0.5px rgb(0, 0, 0);
          }
        }
      }
    }
  }
  .footerImg {
    animation: torch 0.5s infinite;
    animation-direction: alternate;
    animation-timing-function: steps(2, end);
  }
}
@keyframes torch {
  100% {
    transform: translateY(20px);
  }
}
