.container {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex-direction: column;
  height: 100vw;
  @media screen and (max-width: 1024px) {
    height: 110vw;
  }
  @media screen and (max-width: 768px) {
    height: 135vw;
  }
  @media screen and (max-width: 425px) {
    height: 260vw;
  }
  h2 {
    text-shadow: -2px 2px 0 #000, 2px 2px 0 #000, 2px -2px 0 #000,
      -2px -2px 0 #000;
    @media screen and (max-width: 425px) {
      text-shadow: -1px 1px 0 #000, 1px 1px 0 #000, 1px -1px 0 #000,
        -1px -1px 0 #000;
    }
  }
  .content {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    flex-direction: column;
    height: 800px;
    @media screen and (max-width: 1024px) {
      height: 700px;
    }
    & > div {
      width: 700px;
      margin-bottom: 50px;
      text-align: center;
      @media screen and (max-width: 1024px) {
        width: 500px;
      }
      @media screen and (max-width: 425px) {
        width: 90%;
      }
      img {
        width: 100%;
      }
      h4 {
        font-size: 25px;
        cursor: pointer;
        letter-spacing: 2px;
        text-shadow: -2px 2px 0 #000, 2px 2px 0 #000, 2px -2px 0 #000,
          -2px -2px 0 #000;
        @media screen and (max-width: 1024px) {
          font-size: 20px;
        }
        @media screen and (max-width: 425px) {
          text-shadow: -1px 1px 0 #000, 1px 1px 0 #000, 1px -1px 0 #000,
            -1px -1px 0 #000;
          font-size: 15px;
        }
      }
      p {
        color: #000;
        font-size: 22px;
        width: 95%;
        margin: 0 auto;
        height: 0;
        overflow: hidden;
        @media screen and (max-width: 1024px) {
          font-size: 17px;
        }
        @media screen and (max-width: 425px) {
          font-size: 11px;
        }
      }
      &.selected {
        p {
          height: 100%;
        }
      }
    }
  }
  .footerImg {
    animation: torch 0.5s infinite;
    animation-direction: alternate;
    animation-timing-function: steps(2, end);
  }
  .socials {
    padding: 5px;
    transition: all 0.3s;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 40px;
    border-radius: 30px;
    background: rgba(0, 0, 0, 0.39);
    & > div {
      color: #000;
      margin: 0 5px;
      background-color: #fff;
      height: 50px;
      width: 50px;
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 50%;
      font-size: 35px;
      cursor: pointer;
      transition: all 0.2s;
      @media screen and (max-width: 768px) {
        height: 40px;
        width: 40px;
        font-size: 25px;
      }
      &:hover {
        transform: scale(1.05);
      }
      img {
        height: 20px;
        filter: invert(100);
        width: 20px;
      }
    }
  }
}
@keyframes torch {
  100% {
    transform: translate(-20px, 20px);
  }
}
