.container {
  margin-bottom: 50vw;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  text-shadow: -2px 2px 0 #000, 2px 2px 0 #000, 2px -2px 0 #000,
    -2px -2px 0 #000;
  @media screen and (max-width: 768px) {
    margin-bottom: 20vw;
    text-shadow: -1px 1px 0 #000, 1px 1px 0 #000, 1px -1px 0 #000,
      -1px -1px 0 #000;
  }
  @media screen and (max-width: 425px) {
    margin-bottom: 110vw;
  }
  & > p {
    margin-bottom: 40px;
    width: 500px;
    text-align: center;
    letter-spacing: 2px;
    @media screen and (max-width: 768px) {
      margin-bottom: 10px;
    }
    @media screen and (max-width: 425px) {
      width: 100%;
    }
  }
  .grid {
    width: 80%;
    margin: 0 auto;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    column-gap: 20px;
    row-gap: 50px;
    @media screen and (max-width: 1440px) {
      row-gap: 20px;
      width: 60%;
    }
    @media screen and (max-width: 1024px) {
      row-gap: 0px;
    }
    @media screen and (max-width: 768px) {
      grid-template-columns: 1fr 1fr;
      column-gap: 0;
      width: 350px;
    }
    @media screen and (max-width: 425px) {
      width: 80%;
    }
    div {
      padding: 10px;
      display: flex;
      align-items: center;
      justify-content: center;
      flex-direction: column;
      &:nth-child(n + 5) {
        flex-direction: column-reverse;
      }
      &:not(:nth-child(n + 5)) {
        span {
          display: flex;
          align-items: flex-end;
          justify-content: center;
        }
      }
      @media screen and (max-width: 900px) {
        &:nth-child(n + 5) {
          flex-direction: column;
        }
      }
      span {
        font-size: 22px;
        text-align: center;
        margin-bottom: 15px;
        height: 100px;
        letter-spacing: 2px;
        @media screen and (max-width: 1440px) {
          font-size: 18px;
          height: 70px;
        }
        @media screen and (max-width: 1024px) {
          font-size: 11px;
          height: 70px;
          text-shadow: none;
          -webkit-text-stroke: 0.5px rgb(0, 0, 0);
          height: 50px;
        }
        @media screen and (max-width: 768px) {
          height: 30px;
          margin-bottom: 5px;
        }
      }
      img {
        width: 100%;
        transition: all 0.3s;
        @media screen and (max-width: 768px) {
          width: 80%;
        }
        &:hover {
          transform: scale(1.05);
        }
      }
    }
  }
  .footerImg {
    animation: torch 0.5s infinite;
    animation-direction: alternate;
    animation-timing-function: steps(2, end);
  }
}
@keyframes torch {
  100% {
    transform: translateY(20px);
  }
}
