@font-face {
  font-family: "japanese";
  src: url("./fonts/Japanese3017.ttf");
}
@font-face {
  font-family: "comic";
  src: url("./fonts/design.graffiti.comicsansmsgras.ttf");
}
* {
  margin: 0;
  font-family: "japanese", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  padding: 0;
  box-sizing: border-box;
  scroll-behavior: smooth;
  scroll-padding: 100px;
}
body {
  color: #fff;
  overflow-x: hidden;
  background-color: rgb(52, 24, 62);
}

.shuriken {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 50px;
}
.shuriken img {
  height: 130px;
}
@media screen and (max-width: 1024px) {
  .shuriken {
    margin-top: 25px;
  }
  .shuriken img {
    height: 80px;
  }
}
@media screen and (max-width: 768px) {
  .shuriken img {
    height: 50px;
  }
}
