.container {
  position: fixed;
  width: inherit;
  height: auto;
  transition: all 0.3s;
  z-index: 2;
  display: flex;
  align-items: center;
  justify-content: center;
  padding-top: 40px;
  overflow-y: visible !important;
  @media screen and (max-width: 768px) {
    height: 100%;
    background-color: rgba(0, 0, 0, 0.767);
    flex-direction: column;
    transform: translateX(100%);
    &.open {
      transform: none;
    }
  }
  &.shrinked {
    @media screen and (min-width: 769px) {
      background-color: rgba(0, 0, 0, 0.445);
      padding-top: 20px;
    }
  }
  .openBtn,
  .closeBtn {
    position: absolute;
    top: 50px;
    display: none;
    border: 0;
    outline: none;
    height: 30px;
    width: 30px;
    padding: 5px;
    font-size: 20px;
    @media screen and (max-width: 768px) {
      display: flex;
      align-items: center;
      justify-content: center;
    }
  }
  .openBtn {
    left: -100px;
    background-color: #fff;
    border-radius: 50%;
    @media screen and (max-width: 425px) {
      left: -50px;
    }
  }

  .closeBtn {
    left: 100px;
    background: transparent;
    color: #fff;
    @media screen and (max-width: 425px) {
      left: 50px;
    }
  }
  .socials {
    width: 200px;
    transition: all 0.3s;
    display: flex;
    align-items: center;
    justify-content: center;
    & > div {
      margin-left: 10px;
      background-color: #fff;
      height: 30px;
      width: 30px;
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 50%;
      cursor: pointer;
      transition: all 0.2s;
      &:hover {
        transform: scale(1.05);
      }
      img {
        height: 20px;
        filter: invert(100);
        width: 20px;
      }
    }
  }
  ul {
    height: 50px;
    width: 100%;
    list-style-type: none;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    @media screen and (max-width: 768px) {
      flex-direction: column;
      height: auto;
      margin-bottom: 20px;
    }
    a {
      text-decoration: none;
      color: #fff;
    }
    li {
      letter-spacing: 2px;
      text-transform: uppercase;
      font-size: 25px;
      cursor: pointer;
      transition: all 0.3s;
      text-shadow: -2px 2px 0 #000, 2px 2px 0 #000, 2px -2px 0 #000,
        -2px -2px 0 #000;
      @media screen and (max-width: 1024px) {
        font-size: 20px;
      }
      @media screen and (max-width: 768px) {
        margin: 10px 0;
      }
      &:hover {
        transform: scale(1.05);
      }
      &:active {
        transform: translateY(2px);
      }
      &.disabled {
        opacity: 0.4;
        text-shadow: none;
      }
    }
  }
}
