.container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: column;
  padding: 130px 0 70px 0;
  margin-bottom: 35vw;
  @media screen and (max-width: 1920px) {
    margin-bottom: 30vw;
  }
  @media screen and (max-width: 1440px) {
    margin-bottom: 20vw;
  }
  @media screen and (max-width: 1024px) {
    padding-top: 100px;
    margin-bottom: 25vw;
  }
  @media screen and (max-width: 768px) {
    padding-top: 50px;
  }
  @media screen and (max-width: 425px) {
    margin-bottom: 0;
    padding-bottom: 30px;
  }
  h1 {
    font-size: 65px;
    text-align: center;
    line-height: 50px;
    text-transform: uppercase;
    margin-bottom: 20%;
    color: #000;
    text-shadow: -2px 2px 0 #fff, 2px 2px 0 #fff, 2px -2px 0 #fff,
      -2px -2px 0 #fff;
    @media screen and (max-width: 1440px) {
      margin-bottom: 10%;
      font-size: 45px;
      line-height: 40px;
    }
    @media screen and (max-width: 768px) {
      font-size: 35px;
      line-height: 35px;
    }
    @media screen and (max-width: 425px) {
      margin-bottom: 50px;
    }
  }
  .content {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    width: 100%;
    padding: 0 50px;
    @media screen and (max-width: 425px) {
      flex-direction: column;
      padding: 0 10px;
      align-items: flex-end;
    }
    .text {
      width: 600px;
      overflow: visible;
      position: relative;
      text-align: center;
      z-index: 1;
      padding: 3% 2%;
      @media screen and (max-width: 1800px) {
        padding: 35px 30px;
        width: 500px;
      }
      @media screen and (max-width: 1024px) {
        padding: 20px;
        width: 400px;
      }
      @media screen and (max-width: 768px) {
        width: 280px;
        padding: 15px;
      }
      @media screen and (max-width: 425px) {
        width: 100%;
        background-color: #fff;
        border-radius: 30px;
      }
      p {
        font-size: 20px;
        color: #8b3c04;
        line-height: 23px;
        @media screen and (max-width: 1800px) {
          font-size: 18px;
          line-height: 20px;
        }
        @media screen and (max-width: 1024px) {
          font-size: 15px;
          line-height: 16px;
        }
        @media screen and (max-width: 768px) {
          font-size: 11px;
          line-height: 12px;
        }
        span {
          color: #ffd300;
          &:nth-child(1) {
            font-size: 30px;
            text-shadow: 2px 2px 0 #000;
            @media screen and (max-width: 1800px) {
              font-size: 22px;
            }
            @media screen and (max-width: 1024px) {
              font-size: 20px;
            }
            @media screen and (max-width: 768px) {
              font-size: 14px;
            }
          }
          &:nth-child(3) {
            font-size: 25px;
            text-shadow: 1px 1px 0 #000;
            @media screen and (max-width: 1440px) {
              font-size: 20px;
            }
            @media screen and (max-width: 1024px) {
              font-size: 18px;
            }
            @media screen and (max-width: 768px) {
              font-size: 12px;
            }
          }
        }
      }
      &::before {
        content: "";
        top: 0;
        left: 0;
        width: 150%;
        height: 100%;
        position: absolute;
        background: url("../../assets/cloud2.png") no-repeat;
        background-size: auto 100%;
        z-index: -1;
        @media screen and (max-width: 425px) {
          display: none;
        }
      }
    }
    .connectBtn {
      height: 180px;
      width: 200px;
      background: url("../../assets/cloudHome.png") no-repeat;
      background-size: 100% auto;
      color: rgb(255, 98, 105);
      display: flex;
      align-items: center;
      justify-content: center;
      margin-top: 200px;
      margin-right: 50px;
      cursor: default;
      transition: all 0.3s;
      animation: flash 1s infinite;
      //remove next line to show connect button
      opacity: 0;
      @media screen and (max-width: 1024px) {
        width: 150px;
        height: 130px;
      }
      @media screen and (max-width: 425px) {
        margin: 100px 0 0 0;
      }
      &:hover {
        transform: scale(1.1);
      }
      p {
        font-size: 25px;
        @media screen and (max-width: 1024px) {
          font-size: 20px;
        }
      }
    }
  }
}
@keyframes flash {
  0% {
    color: rgb(255, 98, 105);
  }
  40% {
    color: rgb(255, 196, 0);
  }
  80% {
    color: rgb(255, 98, 105);
  }
}
