.container {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  margin-bottom: 65vw;
  @media screen and (max-width: 1920px) {
    margin-bottom: 60vw;
  }
  @media screen and (max-width: 1440px) {
    margin-bottom: 40vw;
  }
  @media screen and (max-width: 425px) {
    margin-bottom: 75vw;
  }
  h2 {
    @media screen and (max-width: 425px) {
      margin-bottom: 70px;
    }
  }
  .content {
    width: 100%;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    background: url("../../assets/house.png") no-repeat;
    background-position: center;
    background-size: 80% auto;
    position: relative;
    @media screen and (max-width: 1024px) {
      background-size: 60% auto;
    }
    @media screen and (max-width: 425px) {
      background: none;
    }
    & > p {
      text-align: center;
      width: 50%;
      @media screen and (max-width: 425px) {
        position: absolute;
        top: -10px;
        left: 50%;
        transform: translate(-50%, -100%);
        width: 90%;
      }
    }
    & > div {
      width: 250px;
      color: #000;
      @media screen and (max-width: 1024px) {
        width: 200px;
      }
      @media screen and (max-width: 425px) {
      }
      & > div {
        padding: 45px 22%;
        text-align: center;
        transition: all 0.4s;
        cursor: default;
        &:hover {
          transform: scale(1.2);
          @media screen and (max-width: 425px) {
            transform: none;
          }
        }
        &:not(:last-child) {
          margin-bottom: 40px;
          @media screen and (max-width: 1024px) {
            margin-bottom: 20px;
          }
        }
        span {
          color: #8b3c04;
          font-size: 20px;
          @media screen and (max-width: 1024px) {
            font-size: 18px;
          }
        }
        p {
          font-size: 20px;
          @media screen and (max-width: 1024px) {
            font-size: 17px;
          }
        }
        ul {
          list-style-type: none;
          margin: 0;
          li {
            text-indent: -5px;
            font-size: 13px;
            @media screen and (max-width: 1024px) {
              font-size: 11px;
            }
            &::before {
              content: "-";
              text-indent: -5px;
            }
            &.brown {
              color: #b45e00;
            }
          }
        }
      }
      &:first-child {
        div {
          &:nth-child(1) {
            background: url("../../assets/roadmap/phase1.png") no-repeat;
            background-size: 100% 100%;
          }
          &:nth-child(2) {
            background: url("../../assets/roadmap/phase3.png") no-repeat;
            background-size: 100% 100%;
          }
        }
      }
      &:last-child {
        div {
          &:nth-child(1) {
            background: url("../../assets/roadmap/phase25.png") no-repeat;
            background-size: 100% 100%;
          }
          &:nth-child(2) {
            background: url("../../assets/roadmap/phase4.png") no-repeat;
            background-size: 100% 100%;
          }
          &:nth-child(3) {
            background: url("../../assets/roadmap/phase25.png") no-repeat;
            background-size: 100% 100%;
          }
        }
      }
    }
  }
  .footerImg {
    animation: pulse 1s infinite;
    animation-timing-function: steps(2, end);
  }
  & > p {
    margin: 40px 0;
    @media screen and (max-width: 1440px) {
      margin: 30px 0;
    }
    @media screen and (max-width: 1024px) {
      margin: 15px 0;
    }
    @media screen and (max-width: 425px) {
      margin: 10px 0;
    }
  }
  .socials {
    width: 200px;
    display: flex;
    align-items: center;
    justify-content: center;
    @media screen and (max-width: 1024px) {
      width: 170px;
    }
    @media screen and (max-width: 768px) {
      width: 130px;
    }
    img {
      width: 40%;
      cursor: pointer;
      transition: all 0.2s;
      margin: 0 10px;
      @media screen and (max-width: 768px) {
        margin: 0 5px;
      }
      &:hover {
        transform: scale(1.05);
      }
    }
  }
}
@keyframes pulse {
  100% {
    opacity: 0.5;
  }
}
