.container {
  margin-bottom: 50vw;
  @media screen and (max-width: 1920px) {
    margin-bottom: 40vw;
  }
  @media screen and (max-width: 1440px) {
    margin-bottom: 20vw;
  }
  @media screen and (max-width: 425px) {
    margin-bottom: 70vw;
  }
  h2 {
    text-shadow: -2px 2px 0 #000, 2px 2px 0 #000, 2px -2px 0 #000,
      -2px -2px 0 #000;
    @media screen and (max-width: 768px) {
      text-shadow: -1px 1px 0 #000, 1px 1px 0 #000, 1px -1px 0 #000,
        -1px -1px 0 #000;
    }
  }
  .board {
    background: url("../../assets/rarity/10.png");
    background-size: 100% 100%;
    width: 65%;
    margin: 40px auto;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 50px 30px;
    @media screen and (max-width: 1440px) {
      width: 90%;
      padding: 35px 30px;
    }
    @media screen and (max-width: 1024px) {
      margin: 0 auto;
    }
    @media screen and (max-width: 425px) {
      width: 100%;
      padding: 30px 15px;
      flex-direction: column-reverse;
    }
    & > div {
      &:first-child {
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
        width: 60%;
        row-gap: 10px;
        @media screen and (max-width: 768px) {
          row-gap: 0;
        }
        @media screen and (max-width: 425px) {
          width: 100%;
        }
        div {
          display: flex;
          align-items: center;
          justify-content: space-between;
          flex-direction: column;
          padding: 10px 0;
          height: 150px;
          @media screen and (max-width: 768px) {
            height: 100px;
          }
          span {
            text-align: center;
            font-size: 14px;
            letter-spacing: 2px;
            text-shadow: -1px 1px 0 #000, 1px 1px 0 #000, 1px -1px 0 #000,
              -1px -1px 0 #000;
            @media screen and (max-width: 1024px) {
              font-size: 12px;
            }
            @media screen and (max-width: 768px) {
              font-size: 10px;
              text-shadow: none;
              -webkit-text-stroke: 0.2px rgb(0, 0, 0);
            }
            @media screen and (max-width: 425px) {
              font-size: 8px;
            }
          }
          img {
            height: 80%;
          }
        }
      }
      &:last-child {
        width: 40%;
        display: flex;
        align-items: center;
        justify-content: center;
        img {
          width: 60%;
        }
      }
    }
  }
  .unkown {
    width: 350px;
    margin: 30px auto;
    position: relative;
    @media screen and (max-width: 1440px) {
      width: 250px;
      margin: 15px auto;
    }
    @media screen and (max-width: 1024px) {
      width: 180px;
      margin: 0 auto;
    }
    @media screen and (max-width: 768px) {
      width: 100px;
    }
    img {
      width: 100%;
    }
  }
  & > p {
    width: 60%;
    margin: 10px auto;
    text-align: center;
    text-shadow: -2px 2px 0 #000, 2px 2px 0 #000, 2px -2px 0 #000,
      -2px -2px 0 #000;
    @media screen and (max-width: 1024px) {
      margin: 5px auto;
      text-shadow: -1px 1px 0 #000, 1px 1px 0 #000, 1px -1px 0 #000,
        -1px -1px 0 #000;
    }
    @media screen and (max-width: 425px) {
      width: 90%;
    }
    &:nth-child(4) {
      span {
        font-size: 40px;
        @media screen and (max-width: 1440px) {
          font-size: 30px;
        }
        @media screen and (max-width: 1024px) {
          font-size: 25px;
        }
        @media screen and (max-width: 768px) {
          font-size: 20px;
        }
        @media screen and (max-width: 425px) {
          font-size: 18px;
        }
      }
    }
    span {
      color: #ffaa00;
    }
    &:last-child {
      margin-top: 30px;
    }
  }
  .footerImg {
    height: 100px;
    animation: blade 1s infinite;
    animation-timing-function: steps(2, end);
    @media screen and (max-width: 1440px) {
      height: 50px;
    }
    @media screen and (max-width: 768px) {
      height: 25px;
    }
  }
}
@keyframes blade {
  100% {
    transform: translateX(15px);
  }
}
