.container {
  width: 100vw;
  background: url("./assets/bg-final.jpg");
  background-size: cover;
  background-position: top;
  position: relative;
  @media screen and (max-width: 425px) {
    background: url("./assets/bg-mobile.jpg") no-repeat;
    background-size: 165% auto;
    background-position: top center;
    background-attachment: scroll;
  }
  & > div {
    padding-inline: 350px;
    @media screen and (max-width: 1920px) {
      padding-inline: 200px;
    }
    @media screen and (max-width: 1440px) {
      padding-inline: 50px;
    }
    @media screen and (max-width: 1024px) {
      padding-inline: 25px;
    }
    @media screen and (max-width: 425px) {
      padding-inline: 20px;
    }
  }
  h2 {
    text-align: center;
    font-size: 60px;
    margin-bottom: 50px;
    letter-spacing: 6px;
    text-transform: uppercase;
    @media screen and (max-width: 1440px) {
      font-size: 50px;
    }
    @media screen and (max-width: 1024px) {
      font-size: 30px;
      margin-bottom: 30px;
    }
    @media screen and (max-width: 768px) {
      margin-bottom: 15px;
      font-size: 25px;
    }
  }
  p {
    font-size: 25px;
    @media screen and (max-width: 1440px) {
      font-size: 22px;
    }
    @media screen and (max-width: 1024px) {
      font-size: 18px;
    }
    @media screen and (max-width: 768px) {
      font-size: 15px;
    }
    @media screen and (max-width: 425px) {
      font-size: 11px;
    }
  }
  @media screen and (max-width: 420px) {
    background-size: 166% auto;
  }
  @media screen and (max-width: 415px) {
    background-size: 167% auto;
  }
  @media screen and (max-width: 410px) {
    background-size: 168% auto;
  }
  @media screen and (max-width: 405px) {
    background-size: 169% auto;
  }
  @media screen and (max-width: 400px) {
    background-size: 170% auto;
  }
  @media screen and (max-width: 395px) {
    background-size: 171.5% auto;
  }
  @media screen and (max-width: 390px) {
    background-size: 173% auto;
  }
  @media screen and (max-width: 385px) {
    background-size: 175% auto;
  }
  @media screen and (max-width: 380px) {
    background-size: 177% auto;
  }
  @media screen and (max-width: 375px) {
    background-size: 179% auto;
  }
  @media screen and (max-width: 370px) {
    background-size: 181% auto;
  }
  @media screen and (max-width: 365px) {
    background-size: 182% auto;
  }
  @media screen and (max-width: 360px) {
    background-size: 186% auto;
  }
  @media screen and (max-width: 355px) {
    background-size: 189% auto;
  }
  @media screen and (max-width: 350px) {
    background-size: 193% auto;
  }
  @media screen and (max-width: 345px) {
    background-size: 195% auto;
  }
  @media screen and (max-width: 340px) {
    background-size: 197% auto;
  }
  @media screen and (max-width: 335px) {
    background-size: 201% auto;
  }
  @media screen and (max-width: 330px) {
    background-size: 207% auto;
  }
  @media screen and (max-width: 325px) {
    background-size: 210% auto;
  }
  @media screen and (max-width: 320px) {
    background-size: 212% auto;
  }
}
